<!--  -->
<template>
  <div>
    <Card>
      <Breadcrumb>
        <BreadcrumbItem to="/">
          <Icon type="ios-home-outline"></Icon>
          
        </BreadcrumbItem>
        <BreadcrumbItem :to="{name:'repairAdmin'}">
          <Icon type="ios-build"/>
          
        </BreadcrumbItem>

        <Tooltip style="float: right" content="" placement="left">
          <Icon style="font-size: 20px" type="ios-help-circle"/>
        </Tooltip>
      </Breadcrumb>

    </Card>

    <Card style="margin-top: 20px">
      <p slot="title"></p>
      <Row>
        <Col span="8" style="text-align: center">
          
          <Input v-model="orderInfo.name" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Select v-model="orderInfo.state" style="width: 250px" clearable>
            <Option v-for="sta in stateList" :value="sta.state" :key="sta.state">
              {{sta.remark }}
            </Option>
          </Select>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Input v-model="orderInfo.addres" placeholder="" style="width: 250px"/>
        </Col>
      </Row>
      <Row style="margin-top: 20px">
        <Col span="8" style="text-align: center">
          
          <Input v-model="orderInfo.phoneNum" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Input v-model="orderInfo.remark" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          id
          <Input v-model="orderInfo.orderId" placeholder="id" style="width: 250px"/>
        </Col>
      </Row>
      <Row style="margin-top: 20px">
        <Col span="8" style="text-align: center">
          id
          <Input v-model="orderInfo.userId" placeholder="id" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          id
          <Input v-model="orderInfo.acceptId" placeholder="id" style="width: 250px"/>
        </Col>
      </Row>
      <Row>
        <Col>
          <div style="float: right; margin-top: 20px">
            <Button @click="search" type="primary" icon="ios-search"></Button>
          </div>
        </Col>
      </Row>
    </Card>

    <Card style="margin-top: 20px">
      <p slot="title"></p>

      <Spin fix v-if="spinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div></div>
      </Spin>
      <Table highlight-row border :columns="orderColumns" :data="orderList"></Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page :total="orderTotal" :page-size="pageSize" @on-change="pageChange"></Page>
        </div>
        <div style="margin: 10px">
          <Icon style="font-size: 18px; color:#2d8cf0; margin-right: 6px" type="md-contacts"/>
          {{orderTotal}}
        </div>
      </div>
    </Card>
  </div>
</template>

<script>
  export default {
    name: "repair-admin",
    data() {
      return {
        // 
        orderInfo: {
          name: '',
          state: '',
          addres: '',
          phoneNum: '',
          remark: '',
          orderId: '',
          userId: '',
          acceptId: ''
        },

        // 
        spinShow: true,
        // 
        pageNum: 1,
        // 
        pageSize: 6,
        // 
        orderTotal: 1,
        // Userid
        userName: '',
        // 
        stateList:
          [
            {
              state: '0',
              remark: ''
            },
            {
              state: '1',
              remark: ''
            },
            {
              state: '2',
              remark: ''
            },
            {
              state: '3',
              remark: ''
            },

          ],
        orderColumns: [
          {
            align: 'center',
            type: 'index',
            title: '#',
            width: 60,
            fixed: 'left'
          },
          {
            align: 'center',
            title: '',
            tooltip: true,
            key: 'name',
            width: 120,
            fixed: 'left'
          },
          {
            align: 'center',
            title: '',
            key: 'gender',
            width: 100,
            filters: [
              {
                label: '',
                value: 1
              },
              {
                label: '',
                value: 2
              }
            ],
            filterMultiple: false,
            filterMethod(value, row) {
              if (value === 1) {
                return row.gender === 'male';

              } else if (value === 2) {
                return row.gender === 'female';
              }
            },
            render: (h, params) => {
              if (params.row.gender === 'female') {
                return h('div', [
                  h('Icon', {
                    props: {
                      type: 'md-female',
                    },
                    style: {
                      fontSize: '18px',
                      color: '#FF6666'
                    }
                  }),
                ]);
              } else {
                return h('div', [
                  h('Icon', {
                    props: {
                      type: 'md-male',
                    },
                    style: {
                      fontSize: '18px',
                      color: '#2b85e4'
                    }
                  }),
                ]);
              }
            }
          },
          {
            align: 'center',
            title: '',
            key: 'state',
            width: 120,
            render: (h, params) => {
              if (params.row.state === this.$store.state.global.repairState.COMMIT_INIT) {

                return h('Badge', {
                  props: {
                    status: 'warning',
                    text: '',
                  }
                });
              } else if (params.row.state === this.$store.state.global.repairState.ACCEPT) {

                return h('Badge', {
                  props: {
                    status: 'processing',
                    text: '',
                  }
                });

              } else if (params.row.state === this.$store.state.global.repairState.SUCCESS) {

                return h('Badge', {
                  props: {
                    status: 'success',
                    text: '',
                  }
                });
              } else if (params.row.state === this.$store.state.global.repairState.CANCEL) {

                return h('Badge', {
                  props: {
                    status: 'error',
                    text: '',
                  }
                });
              }

            }
          },
          {
            align: 'center',
            title: '',
            tooltip: true,
            key: 'addres',
            width: 100
          },
          {
            align: 'center',
            title: '',
            key: 'phoneNum',
            width: 120
          },
          {
            align: 'center',
            title: '',
            key: 'remark',
            tooltip: true,
            width: 100
          },
          {
            align: 'center',
            title: 'id',
            key: 'orderId',
            width: 100,
          },
          {
            align: 'center',
            title: 'id',
            key: 'userId',
            width: 100,
            render: (h, params) => {
              return h('div', [
                h('Tooltip', {
                  props: {
                    content: this.userName,
                  },
                  on: {
                    // Tooltip
                    'on-popper-show': (event) => {
                      this.getUserName(params.row.userId);
                    }
                  }
                }, params.row.userId)
              ]);
            }
          },
          {
            align: 'center',
            title: 'id',
            key: 'acceptId',
            width: 100,
            render: (h, params) => {
              if (params.row.acceptId != '' && params.row.acceptId != undefined) {
                return h('Tooltip', {
                  props: {
                    content: this.userName,
                  },
                  on: {
                    // Tooltip
                    'on-popper-show': (event) => {
                      this.getUserName(params.row.acceptId);
                    }
                  }
                }, params.row.acceptId);
              } else {
                return h('p', {}, '');
              }
            }
          },

          {
            align: 'center',
            title: '',
            key: 'submitTime',
            width: 180,
            render: (h, params) => {
              if (params.row.submitTime != '' && params.row.submitTime != undefined) {
                return h('p', {}, new Date(params.row.submitTime).toLocaleString())
              } else {
                return h('p', {}, "")
              }
            }
          },
          {
            align: 'center',
            title: '',
            key: 'acceptTime',
            width: 180,
            render: (h, params) => {
              if (params.row.acceptTime != '' && params.row.acceptTime != undefined) {
                return h('p', {}, new Date(params.row.acceptTime).toLocaleString())
              } else {
                return h('p', {}, "")
              }
            }
          },
          {
            align: 'center',
            title: '',
            key: 'successTime',
            width: 180,
            render: (h, params) => {
              if (params.row.successTime != '' && params.row.successTime != undefined) {
                return h('p', {}, new Date(params.row.successTime).toLocaleString())
              } else {
                return h('p', {}, '');
              }

            }
          },
          {
            title: '',
            key: 'action',
            align: 'center',
            fixed: 'right',
            width: 150,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.accept(params.row.orderId)
                    }
                  }
                }, ''),
                h('Button', {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.success(params.row.orderId)
                    }
                  }
                }, '')
              ]);
            }
          }
        ],
        orderList: []
      }
    },
    methods: {

      // 
      search: function () {
        this.getOrderList();
      },
      /**
       * 
       * @param orderId
       */
      success: function (orderId) {
        this.$api.repairAdmin.success(orderId).then(
          res => {
            if (res.status == this.$store.state.global.status.OK) {
              // 
              this.getOrderList();
            }
          }
        ).catch(
          error => {
            console.log(error);
          }
        )
      },

      /**
       * 
       * @param orderId
       */
      accept: function (orderId) {
        this.$api.repairAdmin.accept(orderId).then(
          res => {
            if (res.status == this.$store.state.global.status.OK) {
              // 
              this.getOrderList();
            }
          }
        ).catch(
          error => {
            console.log(error);
          }
        )
      },

      /**
       * 
       * @param orderId
       */
      pageChange: function (currentPageNum) {
        this.pageNum = currentPageNum;
        this.getOrderList();
      },


      /**
       * 
       * @param userId
       */
      getUserName(userId) {
        this.$api.user.getUserNameByUserId(userId).then(
          res => {
            this.userName = res.data.userName;
          }
        );
      },

      getOrderList() {

        this.spinShow = true;

        this.$api.repairAdmin.findAllOrder(
          {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            // 
            name: this.orderInfo.name,
            state: this.orderInfo.state,
            addres: this.orderInfo.addres,
            phoneNum: this.orderInfo.phoneNum,
            remark: this.orderInfo.remark,
            orderId: this.orderInfo.orderId,
            userId: this.orderInfo.userId,
            acceptId: this.orderInfo.acceptId,
          }
        ).then(
          res => {
            console.log(res.data[0].list);
            this.orderTotal = res.data[0].total;
            this.orderList = res.data[0].list;

            this.spinShow = false;
          }
        ).catch(
          error => {
            this.spinShow = false;
          }
        );
      }
    },
    created() {
      this.getOrderList();
      // 2
      this.$store.commit('setAdminMenuActive', '2');
    }
  }
</script>

<style scoped lang='less'>
  @import "../../common/less/global";
</style>



