/**
 * 
 * @Author Giovani
 * @Create: 2018/10/19 14:27
 */
export default {
  state: {
    BASE_URL: '/swca_api',

    /**
     * 
     */
    status: {
      /**
       * 
       */
      BAD_REQUEST: 400,
      /**
       * 
       */
      OK: 200,
      /**
       * 
       */
      FORBIDDEN: 403,
      /**
       * 
       */
      INTERNAL_SERVER_ERROR: 500,
      /**
       * 
       */
      GATEWAY_TIMEOUT: 504,
      /**
       * 
       */
      NO_CONTENT: 204,
      /**
       * 
       */
      NOT_FOUND: 404,
    },
    // 
    repairState: {
      /**
       * 
       */
      COMMIT_INIT: 0,
      /**
       * 
       */
      ACCEPT: 1,
      /**
       * 
       */
      SUCCESS: 2,
      /**
       * 
       */
      CANCEL: 3
    },

    /**
     * 
     */
    adminMenuActive: '1',

    /**
     * 
     */
    userCenter: false

  },
  mutations: {
    /**
     * userCenterthis.$store.commit('setUserCenter', 'userCenter')
     * @param state
     * @param userCenter
     */
    setUserCenter(state, userCenter) {
      state.userCenter = userCenter;
    },

    /**
     * tokenthis.$store.commit('setToken', 'token')
     * @param state
     * @param token
     */
    setToken(state, token) {
      state.token = token;
    },

    /**
     * adminMenuActivethis.$store.commit('setAdminMenuActive', 'activeName')
     * @param state
     * @param activeName
     */
    setAdminMenuActive(state, activeName) {
      state.adminMenuActive = activeName;
    }
  }
}



