<!--  -->
<template>

  <Drawer @on-close="close" width="400" :mask-closable="false" title="" :closable="true"
          v-model="$store.state.global.userCenter">

    <Spin fix v-if="spinShow">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div></div>
    </Spin>

    <p>
      {{user.userName ? user.userName : ''}}
      <Input v-model="newUser.userName" placeholder="" clearable style="width: 200px;margin-left: 20px"/>
    </p>
    <p style="margin-top: 20px">
      {{user.phoneNum}}
    </p>
    <p style="margin-top: 20px">
      {{user.eamil ? user.eamil : ''}} <Input v-model="newUser.eamil" placeholder="" clearable
                                                      style="width: 160px;margin-left: 10px"/>
    </p>
    <p style="margin-top: 20px">
      
      <RadioGroup v-model="newUser.gender">
        <Radio label="male"></Radio>
        <Radio label="female"></Radio>
      </RadioGroup>
    </p>
    <p style="margin-top: 20px">
      {{user.stuNum ? user.stuNum : '' }}
      <Button style="margin-left: 20px" size="small" type="primary"></Button>
    </p>
    <div class="demo-drawer-footer">
      <Button type="primary" @click="changeUser()"></Button>
    </div>
  </Drawer>
</template>

<script>
  export default {
    name: "user-center",
    data() {
      return {
        // 
        spinShow: true,
        // 
        user: '',
        // 
        newUser: {
          userName: '',
          eamil: '',
          gender: '',
          userId: ''
        }

      }
    },

    methods: {
      /**
       * 
       */
      changeUser() {

        if (!this.newUser.userName && !this.newUser.eamil && this.newUser.gender == this.user.gender) {
          this.$Message.info("");
          return;
        }

        this.$api.user.changeUser({
          userName: this.newUser.userName,
          eamil: this.newUser.eamil,
          gender: this.newUser.gender,
          userId: this.newUser.userId
        }).then(
          res => {
            this.newUser = {};
            this.$Message.success("");
            // 
            this.getUserInfo();
          }
        )
      },

      /**
       * 
       */
      getUserInfo() {
        this.spinShow = true;
        var userId = sessionStorage.getItem("userId");
        if (userId) {
          this.$api.user.getUserByUserId(userId).then(
            res => {
              this.spinShow = false;
              this.user = res.data.user;
              this.newUser.userId = res.data.user.userId;
              this.newUser.gender = this.user.gender;
            }
          ).catch(
            error => {
              this.spinShow = false;
            }
          );
        }
      },

      /**
       * 
       */
      close() {
        this.$store.commit('setUserCenter', false);
      }
    },
    created() {
      // 
      this.getUserInfo();
    }
  }
</script>

<style scoped lang="less">
  .demo-drawer-footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    border-top: 1px solid #e8e8e8;
    padding: 10px 16px;
    text-align: right;
    background: #fff;
  }
</style>



