<!--  -->
<template>
  <Modal v-model="myPanelPopup" width="360">
    <Spin fix v-if="spinShow">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div></div>
    </Spin>
    <p slot="header" style="text-align:center">
      <span></span>
    </p>
    <div style="text-align:center">
      <Form ref="formUser" :model="formUser" :rules="ruleUser" :label-width="60">
        <FormItem label="" prop="phone">
          <Input type="text" v-model="formUser.phoneNum" placeholder="">
            <Icon type="md-contact" slot="prefix"/>
          </Input>
        </FormItem>
        <FormItem label="" prop="password">
          <Input type="password" v-model="formUser.password" placeholder="">
            <Icon type="md-key" slot="prefix"/>
          </Input>
        </FormItem>
      </Form>
    </div>
    <div slot="footer" style="text-align:center">
      <Button type="primary" @click="login('formUser')"></Button>
      <Button @click="handleReset('formUser')" style="margin-left: 8px"></Button>
      <Button @click="handleReset('formUser')" style="margin-left: 8px">QQ</Button>
    </div>
  </Modal>
</template>

<script>
  export default {
    name: "login-panel",
    data() {
      const validatePhoneNum = (rule, value, callback) => {

        if (value === '' || value === undefined) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      const validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      return {
        // 
        myPanelPopup: this.panelPopup,
        modal_loading: false,
        // router
        oldRouter: "/",
        // 
        spinShow: false,

        formUser: {
          phoneNum: '',
          password: '',
        },

        ruleUser: {
          phoneNum: [
            {validator: validatePhoneNum, trigger: 'blur'}
          ],
          password: [
            {validator: validatePassword, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      /**
       * 
       * @param name
       */
      login(name) {

        this.$refs[name].validate((valid) => {

          if (valid) {

            this.spinShow = true;
            this.$api.user.login(
              {
                phoneNum: this.formUser.phoneNum,
                password: this.formUser.password
              }
            ).then(
              res => {

                // jwtsessionStorage
                sessionStorage.setItem('token', res.data.jwt);

                // jwtsessionStorage
                let claims = res.data.jwt.split(".")[1];
                claims = JSON.parse(Base64.decode(claims));
                sessionStorage.setItem('userName', claims.userName);
                sessionStorage.setItem('phoneNum', claims.phoneNum);
                sessionStorage.setItem('userId', claims.userId);


                this.myPanelPopup = false;
                // // 
                this.$emit("on-isLogin-change", true);
                this.$Message.success("");
                this.spinShow = false;

              }
            ).catch(
              error => {
                this.spinShow = false;
              }
            );
          } else {
            this.$Message.error('');
            this.spinShow = false;
          }
        });
      },

      handleReset(name) {
        this.$refs[name].resetFields();
      }
    },
    props: {

      // 
      panelPopup: {
        type: Boolean,
        default: false,
      },

    },
    watch: {

      // logRegPopupval
      panelPopup(val) {
        this.myPanelPopup = val;

        if (val === false) {
          this.spinShow = false;
        }
      },

      // myLogRegPopup, val
      myPanelPopup(val) {
        // on-panelPopup-changeval
        this.$emit("on-panelPopup-change", val);
      }

    }
  }
</script>

<style scoped lang='less'>
  .demo-spin-icon-load {
    animation: ani-demo-spin 1s linear infinite;
  }
</style>



