<!--  -->
<template>
  <div>
    <Card>
      <Breadcrumb>
        <BreadcrumbItem to="/">
          <Icon type="ios-home-outline"></Icon>
          
        </BreadcrumbItem>
        <BreadcrumbItem :to="{name:'articlePublishAdmin'}">
          <Icon type="ios-navigate"></Icon>
          
        </BreadcrumbItem>
      </Breadcrumb>
    </Card>
    <Card style="margin-top: 20px">
      <Spin fix v-if="spinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div></div>
      </Spin>
      <Input maxlength="150" size="large" v-model="title" placeholder="" clearable
             style="width: 800px;margin-bottom: 20px"/>

      <RadioGroup v-model="articleType" type="button" size="large" style="margin-bottom: 20px;margin-left: 10px">
        <Radio label="0"></Radio>
        <Radio label="1"></Radio>
      </RadioGroup>

      <p slot="title"></p>
      <div id="editor"></div>
      <Button @click="publish" style="margin-top: 20px;" type="primary"></Button>

    </Card>
  </div>
</template>

<script>
  import Editor from 'wangeditor';

  export default {
    name: "article-publish-admin",
    data() {
      return {
        // 
        title: '',
        editor: '',
        spinShow: false,
        // url
        imgUrl: '',
        // 
        articleType: "0"

      }
    },
    created() {
      // 3-2
      this.$store.commit('setAdminMenuActive', '3-2');
    },
    methods: {
      /**
       * 
       */
      publish: function () {
        this.spinShow = true;
        if (this.editor.txt.text() !== '' && this.editor.txt.text() !== undefined && this.title !== '' && this.title !== undefined) {
          this.$api.indexAdmin.articlePublish(
            {
              content: this.editor.txt.html(),
              title: this.title,
              articleType: this.articleType
            }
          ).then(
            res => {
              this.spinShow = false;
              this.editor.txt.html('');
              this.title = '';
              this.$Message.success("");
            }
          ).catch(
            error => {
              this.spinShow = false;
            }
          )
        } else {
          this.spinShow = false;
          this.$Message.error("");
        }
      },
      /**
       * 
       */
      initEditor() {
        /* htmldivid */
        this.editor = new Editor('#editor');
        // this.editor.customConfig.uploadImgShowBase64 = true;   //  base64 
        // 
        this.editor.customConfig.uploadImgServer = '/swca_api/index/articleImg';
        // filenameformdata.append(name, file)
        this.editor.customConfig.uploadFileName = 'file';
        //  5 
        this.editor.customConfig.uploadImgMaxLength = 5;
        //   3M 5M
        this.editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024;

        // 
        this.editor.customConfig.customUploadImg = async (files, insert) => {
          /* files  input  */
          let formData = new FormData();
          formData.append('file', files[0]);
          await this.$api.indexAdmin.uploadArticleImg(formData).then(
            res => {

              this.$Message.success("");
              this.imgUrl = res.data.imgUrl;
            }
          );

          /* insert   url  */
          insert(this.imgUrl)
        };

        /*  */
        this.editor.create();
      },
    },
    mounted() {
      // 
      this.initEditor()
    }
  }
</script>

<style scoped lang="less">
  .w-e-toolbar {
    flex-wrap: wrap;
    -webkit-box-lines: multiple;
  }

  .w-e-toolbar .w-e-menu:hover {
    z-index: 10002 !important;
  }

  .w-e-menu a {
    text-decoration: none;
  }

  .fullscreen-editor {
    position: fixed !important;
    width: 100% !important;
    height: 100% !important;
    left: 0px !important;
    top: 0px !important;
    background-color: white;
    z-index: 9999;
  }

  .fullscreen-editor .w-e-text-container {
    width: 100% !important;
    height: 95% !important;
  }
</style>



