<!--  -->
<template>
  <div>
    <Card>
      <Breadcrumb>
        <BreadcrumbItem to="/">
          <Icon type="ios-home-outline"></Icon>
          
        </BreadcrumbItem>
        <BreadcrumbItem :to="{name:'slideAdmin'}">
          <Icon type="ios-navigate"></Icon>
          
        </BreadcrumbItem>
        <Tooltip style="float: right" content="" placement="left">
          <Icon style="font-size: 20px" type="ios-help-circle"/>
        </Tooltip>
      </Breadcrumb>
    </Card>
    <Card style="margin-top: 20px">
      <Row>
        <Col span="8">
          <Upload
            :before-upload="handleUpload"
            action="">
            <Button icon="ios-cloud-upload-outline">1410px  556px</Button>

          </Upload>

          <div v-if="file !== null">
            <p style="margin-top: 10px; margin-bottom: 10px">
              : {{ file.name }}
            </p>
            <Button type="primary" ghost @click="upload" :loading="loadingStatus">{{ loadingStatus ? '' : '' }}
            </Button>
          </div>
        </Col>
        <Col span="8">

        <span style="margin-left: 20px">
            
            <Select :filterable="true" v-model="articleId" style="width: 250px" clearable>
              <Option style="max-width: 250px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;"
                      v-for="article in articleList" :value="article.articleId" :key="article.articleId">{{article.title }}</Option>
            </Select>
          </span>
        </Col>
      </Row>
    </Card>

    <Card style="margin-top: 20px">
      <p slot="title"></p>
      <Spin fix v-if="spinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div></div>
      </Spin>

      <Table highlight-row border :columns="slideColumns" :data="slideLsit"></Table>
    </Card>

    <Modal v-model="bigImgModal" fullscreen title="">
      <div style="text-align:center">
        <img :src="bigImgAddres" alt="" width="1024px">
      </div>
      <div slot="footer" style="text-align:center">
        <Button type="primary" @click="closeBigImg"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
  export default {
    name: "slide-admin",
    data() {
      return {
        // 
        bigImgAddres: '',
        // 
        bigImgModal: false,
        // 
        articleTitle: '',
        // userName
        userName: '',
        // 
        slideLsit: [],
        // id0
        articleId: 0,
        // 
        spinShow: false,

        // 
        articleList: [],
        file: null,
        loadingStatus: false,

        slideColumns: [
          {
            align: 'center',
            type: 'index',
            title: '#',
          },
          {
            align: 'center',
            title: 'id',
            tooltip: true,
            key: 'slideId',
          },
          {
            align: 'center',
            title: '',
            key: 'addres',
            width: 200,
            render: (h, params) => {
              return h('Tooltip', {
                props: {
                  content: '',
                }
              }, [
                h('img', {
                    attrs: {
                      src: params.row.addres,
                    },
                    style:
                      {
                        width: '160px',
                        marginTop: '6px'
                      },
                    on: {
                      click: () => {
                        this.imgClick(params.row.addres);
                      }
                    }
                  },
                )
              ]);
            }
          },
          {
            align: 'center',
            title: 'ID',
            key: 'userId',
            render: (h, params) => {
              return h('Tooltip', {
                props: {
                  content: this.userName,
                },
                on: {
                  // Tooltip
                  'on-popper-show': (event) => {
                    this.getUserName(params.row.userId);
                  }
                }
              }, params.row.userId);
            }
          },
          {
            align: 'center',
            title: '',
            width: 170,
            key: 'submitTime',
            render: (h, params) => {
              return h('p', new Date(params.row.submitTime).toLocaleString())
            }
          },
          {
            align: 'center',
            title: 'id',
            tooltip: true,
            key: 'articleId',
            render: (h, params) => {
              if (params.row.articleId === 0) {
                return h('span', "");
              }

              return h('Tooltip', {
                props: {
                  content: this.articleTitle,
                  "max-width": "150"
                },
                on: {
                  // Tooltip
                  'on-popper-show': (event) => {
                    this.getAritcleTitle(params.row.articleId);
                  }
                }
              }, params.row.articleId);
            }
          },

          {
            align: 'center',
            title: '',
            key: 'state',
            tooltip: true,
            render: (h, params) => {
              if (params.row.state === 0) {
                return h('Badge', {
                  props: {
                    status: 'warning',
                    text: '',
                  }
                });
              }
              if (params.row.state === 1) {
                return h('Badge', {
                  props: {
                    status: 'processing',
                    text: '',
                  }
                });
              }
              if (params.row.state === 2) {
                return h('Badge', {
                  props: {
                    status: 'error',
                    text: '',
                  }
                });
              }
            }
          },
          {
            title: '',
            key: 'action',
            align: 'center',
            width: 150,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.checkSlide(params.row.slideId)
                    }
                  }
                }, ''),
                h('Poptip', {
                  props: {
                    confirm: true,
                    title: '',
                  },
                  style: {
                    textAlign: 'left'
                  },
                  on: {
                    'on-ok': (event) => {
                      this.deletSlide(params.row.slideId);
                    },
                  }
                }, [

                  h('Button', {
                    props: {
                      type: 'error',
                      size: 'small'
                    },
                    on: {
                      click: () => {
                        this.success(params.row.orderId)
                      }
                    }
                  }, '')
                ]),

              ]);
            }
          }
        ],
      }
    },
    methods: {

      /**
       * 
       */
      checkSlide(slideId) {
        this.spinShow = true;
        this.$api.indexAdmin.checkSlide(slideId).then(
          res => {
            this.spinShow = false;
            // 
            this.findAllSlideImg();
          }
        ).catch(
          error => {
            this.spinShow = false;
          }
        );
      },

      /**
       * 
       */
      deletSlide(slideId) {
        this.spinShow = true;
        this.$api.indexAdmin.deleteSlide(slideId).then(
          res => {
            this.spinShow = false;
            // 
            this.findAllSlideImg();
          }
        ).catch(
          error => {
            this.spinShow = false;
          }
        );
      },
      /**
       * 
       */
      closeBigImg: function () {
        this.bigImgModal = false;
      },

      /**
       * 
       * @param addres 
       */
      imgClick(addres) {
        this.bigImgModal = true;
        this.bigImgAddres = addres;
      },
      /**
       * 
       * @param articleId
       */
      getAritcleTitle(articleId) {
        this.$api.indexAdmin.getArticleTitleByArticleId(articleId).then(
          res => {
            this.articleTitle = res.data.articleTitle;
          }
        );
      }
      ,

      /**
       * 
       * @param userId
       */
      getUserName(userId) {
        this.$api.user.getUserNameByUserId(userId).then(
          res => {
            this.userName = res.data.userName;
          }
        );
      }
      ,

      /**
       * 
       * @param file
       * @returns {boolean}
       */
      handleUpload(file) {
        this.file = file;
        // 
        return false;
      },
      /**
       * 
       */
      upload() {
        this.loadingStatus = true;

        var params = new FormData();

        params.append('file', this.file);
        // id0
        params.append('articleId', this.articleId);

        this.$api.indexAdmin.uploadSlideImg(params).then(
          res => {
            this.loadingStatus = false;
            // 
            this.findAllSlideImg();
            this.$Message.success('')
          }
        ).catch(
          error => {
            this.loadingStatus = false;
          }
        );
      }
      ,
      /**
       * 
       */
      findAllSlideImg() {
        this.spinShow = true;
        this.$api.indexAdmin.findAllSlideImg().then(
          res => {
            this.slideLsit = res.data[0];
            console.log(this.slideLsit);
            this.spinShow = false;
          }
        ).catch(
          error => {
            this.spinShow = false;
          }
        );
      },

      /**
       * id
       */
      findAllArticleIdAndTitle() {
        this.$api.indexAdmin.findAllAritcleIdAndTitle().then(
          res => {
            this.articleList = res.data[0];
          }
        )
      },


    },
    created() {
      // 3-1
      this.$store.commit('setAdminMenuActive', '3-1');

      // 
      this.findAllSlideImg();
      //  id
      this.findAllArticleIdAndTitle();
    }
  }
</script>

<style scoped>

</style>



