<!--  -->
<template>
  <div>
    <Card>
      <Breadcrumb>
        <BreadcrumbItem to="/">
          <Icon type="ios-home-outline"></Icon>
          
        </BreadcrumbItem>
        <BreadcrumbItem :to="{name:'personnelAdmin'}">
          <Icon type="ios-people"/>
          
        </BreadcrumbItem>
      </Breadcrumb>
    </Card>

    <Card style="margin-top: 20px">
      <p slot="title"></p>
      <Row>
        <Col span="8" style="text-align: center">
          
          <Input v-model="userInfo.userName" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Input v-model="userInfo.eamil" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Input v-model="userInfo.phoneNum" placeholder="" style="width: 250px"/>
        </Col>
      </Row>
      <Row style="margin-top: 20px">
        <Col span="8" style="text-align: center">
          
          <Input v-model="userInfo.stuNum" placeholder="" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          id
          <Input v-model="userInfo.userId" placeholder="id" style="width: 250px"/>
        </Col>
        <Col span="8" style="text-align: center">
          
          <Input v-model="userInfo.role" placeholder="" style="width: 250px"/>
        </Col>
      </Row>
      <Row>
        <Col>
          <div style="float: right; margin-top: 20px">
            <Button @click="search" type="primary" icon="ios-search"></Button>
          </div>
        </Col>
      </Row>
    </Card>
    <Card style="margin-top: 20px">
      <p slot="title"></p>

      <Row>
        <Col span="8" style="text-align: center">
          <!--  -->
          <iCircle :size="120"
                   :trail-width="4"
                   :stroke-width="5"
                   :percent="(userTotalCircle / userTotalCircle)*100"
                   stroke-linecap="square"
                   stroke-color="#5cb85c">

            <div class="demo-Circle-custom">
              <p>
                <icon style="font-size: 18px; color:#5cb85c;" type="md-contacts"></icon>
                <i>{{userTotalCircle}}</i>
              </p>
              <span>
            
          </span>
            </div>
            <!--<div class="demo-Circle-custom">-->

            <!--</div>-->
          </iCircle>
        </Col>
        <Col span="8" style="text-align: center">
          <!--  -->
          <iCircle :size="120"
                   :trail-width="4"
                   :stroke-width="5"
                   :percent="(maleTotalCircle / userTotalCircle)*100"
                   stroke-linecap="square"
                   stroke-color="#2b85e4">
            <div class="demo-Circle-custom">
              <p>
                <icon style="font-size: 18px; color:#2b85e4;" type="md-male"></icon>
                {{maleTotalCircle}}
              </p>
              <span>
            {{((maleTotalCircle / userTotalCircle)*100).toFixed(2)}}%
          </span>
            </div>
          </iCircle>
        </Col>
        <Col span="8" style="text-align: center">
          <!--  -->
          <iCircle :size="120"
                   :trail-width="4"
                   :stroke-width="5"
                   :percent="(femaleTotalCircle / userTotalCircle)*100"
                   stroke-linecap="square"
                   stroke-color="#FF6666">
            <div class="demo-Circle-custom">
              <p>
                <icon style="font-size: 18px; color:#FF6666;" type="md-female"></icon>
                {{femaleTotalCircle}}
              </p>
              <span>
            {{((femaleTotalCircle / userTotalCircle)*100).toFixed(2)}}%
          </span>

            </div>
          </iCircle>
        </Col>
      </Row>

    </Card>

    <Card style="margin-top: 20px">
      <p slot="title"></p>

      <Spin fix v-if="spinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div></div>
      </Spin>
      <Table size="large" highlight-row border :columns="userColumns" :data="userList"></Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
          <Page :total="userTotal" :page-size="pageSize" @on-change="pageChange"></Page>
        </div>
      </div>
    </Card>
    <Modal
      title=""
      v-model="authModal"
      class-name="vertical-center-modal">
      <Spin fix v-if="modalSpinShow">
        <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
        <div></div>
      </Spin>
      <Select :clearable=true @on-change="optionChange" v-model="selectModel" style="">
        <Option v-for="auth in authList" :value="auth.roleId" :key="auth.roleId">{{
          auth.remark }}
        </Option>
      </Select>

      <div slot="footer" style="text-align:center">
        <Button type="primary" @click="onOk"></Button>
      </div>
    </Modal>
  </div>

</template>

<script>
  export default {
    name: "personnel-admin",

    data() {
      return {
        // Circle
        userTotalCircle: 0,
        // Circle
        maleTotalCircle: 0,
        // Circle
        femaleTotalCircle: 0,

        // 
        userInfo: {
          userName: '',
          eamil: '',
          phoneNum: '',
          stuNum: '',
          userId: '',
          role: ''
        },
        // 
        userList: [],
        // 
        userRoleList: [],
        // 
        spinShow: true,
        // 
        modalSpinShow: false,
        // 
        pageNum: 1,
        // 
        pageSize: 6,
        // 
        userTotal: 1,
        // 
        authModal: false,
        // id
        roleId: 0,
        // id
        userId: 0,
        // 
        phoneNum: '',

        // 
        authList:
          [
            {
              roleId: '1',
              remark: ''
            },
            {
              roleId: '2',
              remark: ''
            },
            {
              roleId: '3',
              remark: ''
            }
          ],
        selectModel: '',

        userColumns:
          [
            {
              align: 'center',
              type: 'index',
              title: '#',
              width: 60
            },
            {
              align: 'center',
              title: '',
              tooltip: true,
              key: 'userName',
            },
            {
              align: 'center',
              title: '',
              width: 90,
              key: 'gender',
              filters: [
                {
                  label: '',
                  value: 1
                },
                {
                  label: '',
                  value: 2
                }
              ],
              filterMultiple: false,
              filterMethod(value, row) {
                if (value === 1) {
                  return row.gender === 'male';

                } else if (value === 2) {
                  return row.gender === 'female';
                }
              },
              render: (h, params) => {
                if (params.row.gender === 'female') {
                  return h('div', [
                    h('Icon', {
                      props: {
                        type: 'md-female',
                      },
                      style: {
                        fontSize: '18px',
                        color: '#FF6666'
                      }
                    }),
                  ]);
                } else {
                  return h('div', [
                    h('Icon', {
                      props: {
                        type: 'md-male',
                      },
                      style: {
                        fontSize: '18px',
                        color: '#2b85e4'
                      }
                    }),
                  ]);
                }

              }
            },
            {
              align: 'center',
              title: '',
              key: 'eamil',
              width: 200,
              render: (h, params) => {
                if (params.row.eamil) {
                  return h('span', params.row.eamil)
                } else {
                  return h('span', "");
                }
              }
            },
            {
              align: 'center',
              title: '',
              width: 130,
              key: 'phoneNum',
            },
            {
              align: 'center',
              title: '',
              key: 'stuNum',
              width: 140,
              render: (h, params) => {
                if (params.row.stuNum) {
                  return h('span', params.row.stuNum)
                } else {
                  return h('span', "");
                }
              }
            },
            {
              align: 'center',
              title: 'id',
              key: 'userId',
              width: 80
            },
            {
              align: 'center',
              title: '',
              render: (h, params) => {

                let phoneNum = params.row.phoneNum;

                // 
                let authString = this.getAuthString(phoneNum);

                if (authString === '') {
                  return h('Tag', {
                    props: {
                      color: 'geekblue'
                    }
                  }, authString)

                } else if (authString === '') {

                  return h('Tag', {
                    props: {
                      color: 'orange'
                    }
                  }, authString)

                } else if (authString === '') {

                  return h('Tag', {
                    props: {
                      color: 'red'
                    }
                  }, authString)

                }

              }
            },
            {
              title: '',
              key: 'action',
              align: 'center',
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    },
                    style: {
                      marginRight: '5px'
                    },
                    on: {
                      click: () => {
                        this.authModalPopup(params.row.phoneNum, params.row.userId);
                      }
                    }
                  }, '')
                ]);
              }
            }
          ],
      }
    },
    methods: {
      // 
      findAllUserStatistics() {
        this.$api.personnelAdmin.findAllUserStatistics().then(
          res => {
            this.userTotalCircle = res.data[0].total;
            this.maleTotalCircle = res.data[0].male;
            this.femaleTotalCircle = res.data[0].female;
          }
        )
      },

      // 
      search: function () {
        this.getUserList();
      },

      /**
       * 
       * @param value
       */
      onOk: function () {

        if (this.phoneNum === sessionStorage.getItem('phoneNum')) {
          this.$Message.error({
            content: "",
            duration: 10,
            closable: true
          });

          return;
        }

        if (this.roleId === 0) {

          this.$Message.error({
            content: "",
            duration: 10,
            closable: true
          });

        } else {

          this.modalSpinShow = true;

          this.$api.personnelAdmin.updateAuth(this.userId, this.roleId).then(
            res => {
              if (res.status == this.$store.state.global.status.OK) {
                // 
                this.getUserList();
                this.modalSpinShow = false;
                this.authModal = false;
              }
            }
          ).catch(
            error => {
              this.modalSpinShow = false;
            }
          )

        }

      },

      /**
       * 
       * @param value
       */
      optionChange: function (value) {
        this.roleId = value;
      },

      /**
       * 
       * @param phoneNum
       */
      getAuthString: function (phoneNum) {
        for (var i = 0; this.userRoleList.length; i++) {

          if (this.userRoleList[i].phoneNum === phoneNum) {
            if (this.userRoleList[i].roles.length === 1) {
              return "";
            } else if (this.userRoleList[i].roles.length === 2) {
              return "";
            } else if (this.userRoleList[i].roles.length === 3) {
              return "";
            }
          }
        }
      },

      /**
       * 
       * @param userId
       */
      authModalPopup: function (phoneNum, userId) {
        this.phoneNum = phoneNum;
        this.userId = userId;
        this.authModal = true;
      },

      /**
       * 
       * @param currentPageNum
       */
      pageChange: function (currentPageNum) {
        this.pageNum = currentPageNum;
        this.getUserList();
      }
      ,

      getUserList() {
        // 
        // 
        this.userRoleList = [];

        this.spinShow = true;

        this.$api.personnelAdmin.findAllUser(
          {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            // 
            userName: this.userInfo.userName,
            eamil: this.userInfo.eamil,
            phoneNum: this.userInfo.phoneNum,
            stuNum: this.userInfo.stuNum,
            userId: this.userInfo.userId,
            role: this.userInfo.role,
          }
        ).then(
          res => {
            this.userTotal = res.data[0].total;
            this.userList = res.data[0].list;

            for (var i = 0; i < this.userList.length; i++) {

              // 
              this.$api.user.getUserRolesByPhoneNum(this.userList[i].phoneNum).then(
                res => {
                  // userRoleList
                  // 
                  this.userRoleList.push(res.data);

                }
              );
            }
            this.spinShow = false;
          }
        ).catch(
          error => {
            this.spinShow = false;
          }
        );
      }
    }
    ,
    created() {
      // 
      this.findAllUserStatistics();
      // 
      this.getUserList();
      // 1
      this.$store.commit('setAdminMenuActive', '1');
    },

    watch: {
      // 
      authModal(val) {
        this.selectModel = '';
      }
    }
  }
</script>

<style scoped lang="less">
  .vertical-center-modal {
    display: flex;
    align-items: center;
    justify-content: center;

    .ivu-modal {
      top: 0;
    }
  }

  .demo-Circle-custom {

    & p {
      color: #657180;
      font-size: 14px;
      margin: 10px 0 15px;
    }
    & span {
      display: block;
      padding-top: 15px;
      color: #657180;
      font-size: 14px;
      &:before {
        content: '';
        display: block;
        width: 50px;
        height: 1px;
        margin: 0 auto;
        background: #e0e3e6;
        position: relative;
        top: -15px;
      }
    ;
    }
  }
</style>



