import store from '../store';
import Axios from "axios";
import qs from "qs";
import {Message} from "iview"

// 
Axios.interceptors.request.use(
  function (config) {
    // if (config.method === "post") {
    // axiospostform-data,qsform-data
    // config.data = qs.stringify(config.data);

    config.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
    // 
    // config.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';

    // vuextokenjwt
    let token = sessionStorage.getItem('token');
    config.headers.Authorization = "Bearer " + token;

    // }
    return config;
  },
  function (error) {
    Message.error({message: '!'});
    // 
    return Promise.reject(error);
  }
);

// 
Axios.interceptors.response.use(
  function (response) {

    if (response.status == store.state.global.status.NO_CONTENT) {
      console.log(response);
      Message.error({
        content: "",
        duration: 10,
        closable: true
      });
    }

    // 
    return response;
  },
  function (error) {
    if (error.response.status == store.state.global.status.GATEWAY_TIMEOUT || error.response.status == store.state.global.status.NOT_FOUND) {
      Message.error({
        content: '',
        duration: 10,
        closable: true
      });
    } else if (error.response.status == store.state.global.status.FORBIDDEN) {
      Message.error({
        content: '',
        duration: 10,
        closable: true
      });

    } else if (error.response.status == store.state.global.status.BAD_REQUEST) {
      Message.error({
        content: error.response.data.msg,
        duration: 10,
        closable: true
      });
    } else if (error.response.status == store.state.global.status.INTERNAL_SERVER_ERROR) {
      Message.error({
        content: error.response.data.msg,
        duration: 10,
        closable: true
      });
    }
    // 
    return Promise.reject(error);
  });

// axiosHOST
export const BASE_URL = store.state.global.BASE_URL; // config/index.js
export const axios = Axios; // 



