<!--  -->
<template>
  <Modal v-model="myPanelPopup" width="360">

    <Spin fix v-if="spinShow">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div></div>
    </Spin>

    <p slot="header" style="text-align:center">
      <span></span>
    </p>
    <div style="text-align:center">
      <Form ref="formUser" :model="formUser" :rules="ruleUser" :label-width="60">
        <FormItem label="" prop="phoneNum">
          <Input type="text" v-model="formUser.phoneNum" placeholder="">
            <Icon type="md-contact" slot="prefix"/>
          </Input>
        </FormItem>
        <FormItem label="" prop="userName">
          <Input type="text" v-model="formUser.userName" placeholder="">
            <Icon type="md-person" slot="prefix"/>
          </Input>
        </FormItem>
        <FormItem label="" prop="password">
          <Input type="password" v-model="formUser.password" placeholder="6~18">
            <Icon type="md-key" slot="prefix"/>
          </Input>
        </FormItem>
        <FormItem label="" prop="verifyPassword">
          <Input type="password" v-model="formUser.verifyPassword" placeholder="">
            <Icon type="md-key" slot="prefix"/>
          </Input>
        </FormItem>
      </Form>
    </div>
    <div slot="footer" style="text-align:center">
      <Button type="primary" @click="register('formUser')"></Button>
    </div>
  </Modal>
</template>

<script>
  export default {
    name: "register-panel",
    data() {
      const validatePhoneNum = (rule, value, callback) => {
        // 
        let regexp = /^(13[0-9]|14[0-9]|15[0-9]|166|17[0-9]|18[0-9]|19[8|9])\d{8}$/;

        if (value === '' || value === undefined) {
          callback(new Error(''));
        } else if (!regexp.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      // 
      const validateUserName = (rule, value, callback) => {
        // 
        let regexp = /^[\u4E00-\u9FA5A-Za-z0-9_]+$/;

        if (!(value === '' || value === undefined)) {
          if (!regexp.test(value)) {
            callback(new Error(''));
          } else {
            callback();
          }
        } else {
          callback();
        }
      };

      const validatePassword = (rule, value, callback) => {
        // 6~18
        let regexp = /^[a-zA-Z]\w{5,17}$/;

        if (value === '' || value === undefined) {
          callback(new Error(''));
        } else if (!regexp.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      const validateVerifyPassword = (rule, value, callback) => {
        // 6~18
        let regexp = /^[a-zA-Z]\w{5,17}$/;

        if (value === '' || value === undefined) {
          callback(new Error(''));
        } else if (!regexp.test(value)) {
          callback(new Error(''));
        } else if (!(this.formUser.password === value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      return {
        // 
        myPanelPopup: this.panelPopup,
        modal_loading: false,
        // 
        spinShow: false,

        formUser: {
          phoneNum: '',
          password: '',
          verifyPassword: '',
          userName: '',
        },
        ruleUser: {
          phoneNum: [
            {required: true, validator: validatePhoneNum, trigger: 'blur'}
          ],
          password: [
            {required: true, validator: validatePassword, trigger: 'blur'}
          ],
          verifyPassword: [
            {required: true, validator: validateVerifyPassword, trigger: 'blur'}
          ],
          userName: [
            {validator: validateUserName, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      /**
       * 
       * @param name
       */
      register(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {

            this.spinShow = true;

            this.$api.user.reg({

              // 
              phoneNum: this.formUser.phoneNum,
              password: this.formUser.password,
              userName: this.formUser.userName

            }).then(
              res => {
                console.log(res.data);
                this.$Message.success('');
                this.myPanelPopup = false;
                this.spinShow = false;
                this.$refs['formUser'].resetFields();
              }
            ).catch(
              error => {
                this.spinShow = false;
              }
            );
          } else {
            this.$Message.error('');
            this.spinShow = false;
          }
        });
      },
      handleReset(name) {
        this.$refs[name].resetFields();
      }
    },
    props: {

      // 
      panelPopup: {
        type: Boolean,
        default:
          false,
      },

    },
    watch: {

      // panelPopupval
      panelPopup(val) {
        this.myPanelPopup = val;

        if (val === false) {
          this.spinShow = false;
        }
      },

      // myPanelPopup, val
      myPanelPopup(val) {
        // on-panelPopup-changeval
        this.$emit("on-panelPopup-change", val);
      }

    }
  }
</script>

<style scoped lang="less">
  @import "../../common/less/global";

</style>



