<!--  -->
<template>
  <div class="full-center">
    <Spin fix v-if="spinShow">
      <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
      <div></div>
    </Spin>
    <div style="padding: 40px; width: 80%">
      <Card :bordered="false">
        <p slot="title"></p>
        <Alert type="warning" show-icon>
          
          <template slot="desc">
            
          </template>
        </Alert>
        <Form ref="formOrder" :model="formOrder" :rules="ruleOrder" :label-width="80">
          <FormItem label="" prop="name">
            <Input v-model="formOrder.name" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="phoneNum">
            <Input v-model="formOrder.phoneNum" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="addres">
            <Input v-model="formOrder.addres" placeholder="A-2-203"></Input>
          </FormItem>
          <FormItem label="" prop="gender">
            <RadioGroup v-model="formOrder.gender">
              <Radio label="male"></Radio>
              <Radio label="female"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="" prop="remark">
            <Input v-model="formOrder.remark" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                   placeholder=""></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="handleSubmit('formOrder')"></Button>
            <Button @click="handleReset('formOrder')" style="margin-left: 8px"></Button>
          </FormItem>
        </Form>
      </Card>
    </div>
  </div>
</template>

<script>
  export default {
    name: "commit",
    data() {
      const validatePhoneNum = (rule, value, callback) => {
        // 
        let regexp = /^(13[0-9]|14[0-9]|15[0-9]|166|17[0-9]|18[0-9]|19[8|9])\d{8}$/;

        if (value === '' || value === undefined) {
          callback(new Error(''));
        } else if (!regexp.test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
      };

      return {
        // 
        spinShow: false,
        formOrder: {
          name: '',
          gender: '',
          phoneNum: '',
          addres: '',
          remark: ''
        },
        ruleOrder: {
          name: [
            {required: true, message: "", trigger: 'blur'}
          ],
          gender:
            [
              {required: true, message: "", trigger: 'blur'}
            ],
          phoneNum:
            [
              {required: true, validator: validatePhoneNum, trigger: 'blur'}
            ],
          addres:
            [
              {required: true, message: "", trigger: 'blur'}
            ],
          remark:
            [
              {required: true, message: "", trigger: 'blur'},
            ]
        }
      }
    },
    methods: {
      /**
       * 
       * @param name
       */
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {

            this.spinShow = true;

            this.$api.repair.commit({
              name: this.formOrder.name,
              gender: this.formOrder.gender,
              phoneNum: this.formOrder.phoneNum,
              addres: this.formOrder.addres,
              remark: this.formOrder.remark
            }).then(
              res => {

                this.$Message.success({
                  content: '',
                  duration: 10,
                  closable: true
                });
                this.$refs[name].resetFields();
                this.spinShow = false;

              }
            ).catch(
              error => {
                this.spinShow = false;
              }
            );
          } else {

            this.$Message.error({
              content: '',
              duration: 10,
              closable: true
            });

            this.spinShow = false;
          }
        })
      }
      ,
      handleReset(name) {
        this.$refs[name].resetFields();
      }
    }
  }
</script>

<style scoped lang='less'>
  @import "../../../common/less/global";
</style>



